#projectCard {
    margin: 2vh auto 0vh auto;
    cursor: pointer;
    width: 20em;
    height: 15em;
    color: #161925;
}

#title {
    font-family: Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    line-height: 1.5;
    font-size: 1.2em;
    font-weight: bold;
    display: block;
}

#cardTop {
    height: 50%;
    border-bottom: 1px solid #c1c1c1;
}

#blurb {
    font-family: Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    line-height: 1.5;
    width: 80%;
    margin: 0px
}

#img {
    width: 300px;
    margin: auto;
    display: block;
}

a:hover {
    text-decoration: none;
}

.card-body:hover {
    text-decoration: none;
    color: #b1769b;
}

/**
    use these styles for devices no larger than
    800px and no smaller then 320px
**/
@media (max-width: 800px) and (min-width: 320px){
    #projectCard {
        margin: 5% auto 0 auto;
        width: 85%;
    }
}



