.wireframes {
    width: 100%;
    height: 450px;
}

@media (max-width: 414px) and (min-width: 320px) {
    .wireframes {
        width: 100%;
        height: 200px;
    }
}

@media (max-width: 1024px) and (min-width: 768px) {
    .wireframes {
        width: 100%;
        height: 400px;
    }
}




